body {
    background-color: #fbfbfb;
  }
  
  @media (min-width: 992px){
    .navbar-expand-lg .navbar-toggler {
    display:block
}}
  @media (min-width: 992px) {
    .sidebar {
      height: unset!important;
    }
  }
  
  /* Sidebar */
  .sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    padding: 58px 0 0;
    /* Height of navbar */
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 5%), 0 2px 10px 0 rgb(0 0 0 / 5%);
    width: 240px;
    /* z-index: 600; */
  }
  
  .sidebar.show {
    height: 100vh;
  }
  
  @media (max-width: 992px) {
    .sidebar {
      width: 100%;
      z-index: 2;
    }
  }
  
  .sidebar .active {
    border-radius: 5px;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
  }
  
  .sidebar-sticky {
    position: relative;
    top: 0;
    height: calc(100vh - 48px);
    padding-top: 0.5rem;
    overflow-x: hidden;
    overflow-y: auto;
    /* Scrollable contents if viewport is shorter than content. */
  }
.brand{
    font-weight: bold;
    height:30px;
}
.navbar{
    z-index: 3;
    position: sticky;
    top: 0;
    left: 0;
    right:0;
}
.navbar-light .navbar-brand{
    color: #007bff;
}